<template>
  <div class="main_navs">
    <Header></Header>

    <div class="container">
      <div class="login">
        <div class="row">
          <div class="col-xl-5">
            <div class="log_img">
              <div class="log_img_title">{{$t('reg')}}</div>
              <img src="../../assets/images/llaptop.png" alt="">
            </div>
          </div>
          <div class="col-xl-7">
            <div class="log_form">
              <div>
                <form @submit.prevent="forgotPassword()">
                  <label>{{$t('email')}} *</label>
                  <div class="input_text">
                    <input type="email" v-model="email" placeholder="Email">
                  </div>
                  <div class="forgot_password">
                  </div>
                  <div class="log_btn">
                    <button> Parolni tiklash</button>
                  </div>
                  <div class="phone_link">
                    <router-link to="/post_phone" >
                      <div>
                        Telefon raqami orqali
                      </div>
                    </router-link>
                  </div>
                </form>
                <div class="reg_links">
                  <router-link to="/phone_log">
                    <div>{{$t('reg_quiz')}}? <span>{{$t('login')}}</span></div>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
export default {
  components: {Footer, Header},
  data(){
    return{
      email:'',
      password:''
    }
  },
  methods:{
    forgotPassword() {
      // Create a FormData object
      const formData = new FormData();
      formData.append('email', this.email);

      // Send the POST request with axios
      this.$axiosDefault.post('https://api-kasb.maktab.uz/api/v1/students/forgot-password/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
          .then(() => {
            // Show success toast notification
            this.$toast("Email yuborildi", {
              timeout: 1000,
              type: "success"
            });
            // Redirect to the login page
            this.$router.push('/login');
          })
          .catch(() => {
            // Show error toast notification
            this.$toast("Emailga xabar yuborildi", {
              timeout: 1000,
              type: "success"
            });
            // Clear the email field (if needed)
            this.email = '';
          });
    }

  }

}
</script>
